import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
  return (
    <div className={s.item}>
      <img alt={'writer'} src='data:image/jpeg;base64,' />
      {props.message}
      <div>
        <span>like</span> ({props.likesCount})
      </div>
    </div>
  )
}

export default Post;